const boundingOffset = (element) => {
    if (!element.getBoundingClientRect) {
        return {
            bottom: element.innerHeight,
            left: 0,
            right: element.innerWidth,
            top: 0
        };
    }

    const { bottom, left, right, top } = element.getBoundingClientRect();

    return {
        bottom,
        left,
        right,
        top
    };
};

export default boundingOffset;
